<template>
    <div>
        <div :class="['modal', 'fade']" id="modalPlaceReservationDetails" tabindex="-1" role="dialog"
             aria-labelledby="modalPlaceReservationForm" aria-hidden="true">
            <div class="modal-dialog modal-dialog-centered" role="document">
                <div class="modal-content">
                    <div class="modal-header">
                        <h5 class="modal-title" id="titlePlaceReservationForm">Reservado para a data
                            {{ moment(date).format('DD/MM/YYYY')}} </h5>
                        <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                            <span aria-hidden="true">&times;</span>
                        </button>
                    </div>
                    <div class="modal-body">
                        <table class="table">
                            <tr>
                                <th width="10%">Cliente</th>
                                <td>{{placeReservation.user.hasOwnProperty('name') ? placeReservation.user.name : ''}}</td>
                            </tr>
                            <tr>
                                <th width="10%">Local</th>
                                <td>{{placeReservation.place.hasOwnProperty('name') ? placeReservation.place.name : ''}}</td>
                            </tr>
                            <tr>
                                <th width="10%">Esporte</th>
                                <td>{{placeReservation.sport.hasOwnProperty('name') ? placeReservation.sport.name : ''}}</td>
                            </tr>
                            <tr>
                                <th width="10%">Horário</th>
                                <td>{{placeReservation.schedule }}</td>
                            </tr>

                            <tr>
                                <th width="10%">Horas</th>
                                <td>{{placeReservation.hours_quantity}}</td>
                            </tr>

                            <tr>
                                <th width="20%">Link para convite</th>
                                <td><a target="_blank" :href="placeReservation.public_link">Acessar</a></td>
                            </tr>
                        </table>
                        <a  data-toggle="collapse" href="#guests" role="button" aria-expanded="false" aria-controls="multiCollapseExample1">Convidados</a>
                        <div class="row">
                            <div class="col">
                                <div class="collapse multi-collapse" id="guests">
                                    <div class="card card-body">
                                        <ul id="invites">

                                            <li v-for="user in placeReservation.guests">
                                                {{ user.name }}
                                            </li>
                                        </ul>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>

                    <div class="modal-footer">
                        <button class="btn btn-xs btn-danger">Cancelar</button>
                    </div>
                </div>
            </div>
        </div>

    </div>
</template>

<script>

    import moment from 'moment'
    import PlaceReservationModel from "../models/PlaceReservationModel";

    export default {
        props: {
            placeReservationId: {
                type: String,
                required: false
            },
            date: {
                type: String,
                required: false
            },
        },
        data() {
            return {
                placeReservation: new PlaceReservationModel()
            }
        },
        watch: {
            placeReservationId: function (val) {
                if (val) {
                    $("#modalPlaceReservationDetails").modal('toggle')
                    this.get(val)
                }
            }
        },
        methods:

            {
                moment: moment,
                async get() {
                    this.placeReservation = await PlaceReservationModel.get(this.placeReservationId)
                    console.log(this.placeReservation)
                }


            },
        mounted() {
        }
    }

</script>

