import  moment from 'moment'
class ReservationModel {

    constructor(object) {

        this.id = object.id
        this.title = ' - ' + object.place.name + " - " + object.user.name;
        this.date = moment(object.date + " "+  object.schedule).toDate();
        this.backgroundColor = 'green';
        this.extendedProps = {object: object}
    }

}


export default ReservationModel;
