<template>
    <!-- Modal -->
    <div class="modal fade" id="modalPlaceReservationForm" tabindex="-1" role="dialog"
         aria-labelledby="modalPlaceReservationForm" aria-hidden="true">
        <div class="modal-dialog modal-dialog-centered" role="document">
            <div class="modal-content">
                <div class="modal-header">
                    <h5 class="modal-title" id="titlePlaceReservationForm">Cadastro de Reserva
                        {{ moment(date).format('DD/MM/YYYY')}} </h5>
                    <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                        <span aria-hidden="true">&times;</span>
                    </button>
                </div>
                <div class="modal-body">
                    <form @submit.prevent="submit">

                        <div class="card">
                            <div class="card-body">

                                <!-- sport -->
                                <div class="form-row">
                                    <div class="form-group col-md-12">
                                        <label>Esporte *</label>
                                        <v-select :options="place.sports"
                                                  id="id"
                                                  :reduce="option => option.id"
                                                  label="name"
                                                  placeholder="Escolha um Esporte"
                                                  v-model="fields.sport_id">
                                        </v-select>


                                        <div v-if="errors && errors.sport_id" class="text-danger">{{ errors.sport_id[0]
                                            }}
                                        </div>
                                    </div>
                                </div>


                                <!-- user -->
                                <div class="form-row">
                                    <div class="form-group col-md-12">
                                        <label>Usuário *</label>

                                        <v-select :options="users"
                                                  :id="'id'"
                                                  :reduce="option => option.id"
                                                  label="name"
                                                  placeholder="Escolha um Cliente"
                                                  v-model="fields.user_id">
                                        </v-select>

                                        <div v-if="errors && errors.user_id" class="text-danger">{{ errors.user_id[0]
                                            }}
                                        </div>
                                    </div>
                                </div>

                                <!-- horário-->
                                <div class="form-row">
                                    <div class="form-group col-md-12">
                                        <label>Horários</label>
                                        <input type="time" class="form-control" v-model="fields.schedule">
                                        <div v-if="errors && errors.schedule" class="text-danger">{{ errors.schedule[0]
                                            }}
                                        </div>
                                    </div>
                                </div>

                                <!-- quantidade de horas-->
                                <div class="form-row">
                                    <div class="form-group col-md-12">
                                        <label>Quantidade de Horas</label>
                                        <input type="text" class="form-control" v-model="fields.hours_quantity">
                                        <div v-if="errors && errors.hours_quantity" class="text-danger">{{
                                            errors.hours_quantity[0] }}
                                        </div>
                                    </div>
                                </div>


                                <button class="btn btn-primary" id="bt_salvar" type="submit">
                                    <i class="fa fa-save"></i>
                                    <span>
                        Salvar
                    </span>
                                </button>

                                <button class="btn btn-secondary" id="" type="button" v-if="!this.id > 0"
                                        @click="store()"
                                >
                                    <i class="fa fa-save"></i>
                                    <span>
                        Salvar e cadastrar novo
                    </span>
                                </button>

                            </div>

                        </div>
                    </form>

                </div>
            </div>
        </div>
    </div>

</template>

<script>

    import PlaceReservationModel from "../models/PlaceReservationModel";
    import vSelect from 'vue-select';
    import 'vue-select/dist/vue-select.css';
    import PlaceModel from "../models/PlaceModel";
    import UserModel from "../models/UserModel";
    import moment from 'moment'

    export default {

        name: "PlaceReservationForm",
        components: {vSelect},
        props: {
            placeId: {
                type: String,
                required: true
            },
            id: {
                type: String,
                required: false
            },
            date: {
                type: String,
                required: true
            }
        },
        data() {
            return {

                loaded: true,
                errors: {},
                fields: new PlaceReservationModel(),
                method: 'POST',
                itemOptions: [],
                place: new PlaceModel(),
                users: []
            }
        },
        methods: {
            moment: moment,
            submit() {
                this.fields.place_id = this.placeId
                this.fields.date = moment(this.date).format('DD/MM/YYYY')
                return this.save();
            },
            save() {
                this.errors = {};

                let url = this.$root.URL_BASE + "/places/" + this.placeId + "/place-reservation/";

                axios.post(url, this.fields)
                    .then(() => {
                        //$("#modalPlaceReservationForm").modal('toggle')
                        //this.fields = new PlaceReservationModel();
                        showMessage('s', 'Salvo com sucesso');
                        this.$emit('created')
                    })
                    .catch(error => {

                        if (error.response.status === 500) {
                            showMessage('e', 'Não foi possível completar requisição');
                        }


                        if (error.response.status === 401) {
                            showMessage('w', error.response.data.server_error);
                        }

                        if (error.response.status === 400) {
                            this.errors = error.response.data.errors || {};
                            showMessage('w', 'Preencha todos os campos obrigatórios.');
                        }
                    })
                    .finally(() => true)
            },

            getPlace() {
                let url = this.$root.URL_BASE + "/places/" + this.placeId;
                axios.get(url)
                    .then((response) => {
                        this.place = PlaceModel.fromJson(response.data.data)
                    })
                    .catch(error => {

                        showMessage('w', "Não foi possível carregar");

                    })
                    .finally(() => true)
            },

            async getClients() {
                this.users = await UserModel.list()
            }

        }
        ,
        mounted() {
            this.getPlace()
            this.getClients()
        }
    }

</script>

