<template>
    <div>
        <FullCalendar
            defaultView="dayGridMonth"
            :plugins="calendarPlugins"
            locale="pt"
            @dateClick="handleDateClick"
            @eventClick="eventCLick"
            :weekends="false"
            :events="reservations"
        />

        <place-reservation-details-component
            :place-reservation-id="placeReservationId" :date="date"></place-reservation-details-component>

    </div>
</template>

<script>

    import FullCalendar from '@fullcalendar/vue'
    import dayGridPlugin from '@fullcalendar/daygrid'
    import Interaction from '@fullcalendar/interaction'
    import moment from 'moment'
    import ReservationModel from "../models/ReservationModel";

    export default {
        props: {
            placeId: {
                type: String,
                required: false
            },
        },
        components: {
            FullCalendar // make the <FullCalendar> tag available
        },
        data() {
            return {
                calendarPlugins: [dayGridPlugin, Interaction],
                date: '',
                reservations: [],
                placeReservationId: null,
            }
        },
        methods:

            {
                moment: moment,
                handleDateClick(arg) {
                },

                eventCLick(arg) {
                    this.placeReservationId = null
                    this.date = moment(arg.event.start).format('YYYY-MM-DD')
                    var app = this;
                    setTimeout(function () {
                        app.placeReservationId = arg.event.id
                    }, 100)
                },

                loadReservations() {

                    let url = this.$root.URL_BASE + "/places/place-reservation/list";
                    axios.get(url)
                        .then((response) => {
                            this.reservations = response.data.data.map(function (placeReservationModel) {
                                return new ReservationModel(placeReservationModel)
                            })
                        })
                        .catch(error => {

                            showMessage('w', "Não foi possível carregar as reservas");

                        })
                        .finally(() => true)
                }


            },
        mounted() {
            this.loadReservations()
        }
    }

</script>

<style lang='scss' scoped>

    @import '~@fullcalendar/core/main.css';
    @import '~@fullcalendar/daygrid/main.css';

</style>
