import SportModel from "./SportModel";

class PlaceModel {

    constructor() {

        this.id = null;
        this.name = null;
        this.description = null;
        this.sports = new Array();

    }


    static fromJson(object) {
        let place = new PlaceModel()
        place.id = object.id
        place.name = object.name
        place.description = object.description
        place.sports = object.sports.map(function (sport) {
            return SportModel.fromJson(sport)
        })

        return place

    }
}

export default PlaceModel;
