import {fromJson, URL_BASE} from "./helper";
import axios from "axios";
import UserModel from "./UserModel";
import PlaceModel from "./PlaceModel";
import SportModel from "./SportModel";

class PlaceReservationModel {


    constructor() {

        this.id = null;
        this.place_id = null;
        this.sport_id = null;
        this.user_id = null;
        this.date = null;
        this.schedule = null;
        this.hours_quantity = null;
        this.public_link = null;
        this.user = new UserModel();
        this.place = new PlaceModel();
        this.sport = new SportModel();
        this.guests = [];
    }

    static fromJson(object) {
        return fromJson((new PlaceReservationModel), object);
    }

    static async get(id) {
        let url = URL_BASE + '/places/place-reservation/' + id;
        var model = {}
        await axios.get(url)
            .then((response) => {
                model = PlaceReservationModel.fromJson(response.data.data);
            })
            .catch(error => {
                console.error(error);
                showMessage('w', "Não foi possível carregar");

            })
            .finally(() => true)

        return model
    }

}

export default PlaceReservationModel;
